import Vue from 'vue'
import Router from 'vue-router'

import index from '@/pages/index.vue'
import layout from '@/pages/layout.vue'
import button from '@/pages/button.vue'
import cell from '@/pages/cell.vue'
import popup from '@/pages/popup.vue'

Vue.use(Router)

export default new Router({
  routes: [
    // 开发指南
    {
      path: '/',
      name: '主页',
      component: index
    },
    // 基础组件
    {
      path: '/layout',
      label: '基础组件',
      name: 'Layout 布局',
      component: layout
    },
    {
      path: '/button',
      label: '基础组件',
      name: 'Button 按钮',
      component: button
    },
    {
      path: '/cell',
      label: '基础组件',
      name: 'Cell 单元格',
      component: cell
    },
    {
      path: '/popup',
      label: '基础组件',
      name: 'Popup 弹出层',
      component: popup
    },
    // 表单组件
    {
      path: '/checkbox',
      label: '表单组件',
      name: 'Checkbox 复选框',
      component: index
    },
    {
      path: '/field',
      label: '表单组件',
      name: 'Field 输入框',
      component: index
    },
    {
      path: '/switch',
      label: '表单组件',
      name: 'Switch 输入框',
      component: index
    },
    {
      path: '/rate',
      label: '表单组件',
      name: 'Rate 评分',
      component: index
    },
    {
      path: '/checker',
      label: '表单组件',
      name: 'Checker 选择器',
      component: index
    },
    {
      path: '/upload',
      label: '表单组件',
      name: 'Upload 上传图片',
      component: index
    },
    {
      path: '/range',
      label: '表单组件',
      name: 'Range 滑块',
      component: index
    },
    {
      path: '/select',
      label: '表单组件',
      name: 'Select 选择器',
      component: index
    },
    // 展示组件
    {
      path: '/swipe',
      label: '展示组件',
      name: 'Swipe 轮播',
      component: index
    },
    {
      path: '/loading',
      label: '展示组件',
      name: 'Loading 加载动画',
      component: index
    },
    {
      path: '/collapse',
      label: '展示组件',
      name: 'Collapse 折叠面板',
      component: index
    },
    {
      path: '/progress',
      label: '展示组件',
      name: 'Progress 进度条',
      component: index
    },
    {
      path: '/price',
      label: '展示组件',
      name: 'Price 价格',
      component: index
    },
    {
      path: '/panel',
      label: '展示组件',
      name: 'Panel 面板',
      component: index
    },
    {
      path: '/recycleList',
      label: '展示组件',
      name: 'RecycleList 滚动列表',
      component: index
    },
    // 反馈组件
    {
      path: '/toast',
      label: '反馈组件',
      name: 'Toast 轻提示',
      component: index
    },
    {
      path: '/dialog',
      label: '反馈组件',
      name: 'Dialog 弹出框',
      component: index
    },
    {
      path: '/indicator',
      label: '反馈组件',
      name: 'Indicator 加载提示',
      component: index
    },
    // 导航组件
    {
      path: '/tabs',
      label: '导航组件',
      name: 'Tabs 标签页',
      component: index
    },
    {
      path: '/header',
      label: '导航组件',
      name: 'Header 顶部导航栏',
      component: index
    },
    {
      path: '/search',
      label: '导航组件',
      name: 'Search 搜索框',
      component: index
    },
  ]
})


